import React, { Children } from 'react'
import {Container, Row, Col} from 'react-bootstrap'



function FormContainer({children}) {
  return (
    <Container>
        <Row classname="justy-content-md-center">
            <Col xs={12} md={6}>
                {children}
            </Col>
        </Row>
    </Container>
  )
}

export default FormContainer