import React, {useEffect, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {Link, useParams, useNavigate} from 'react-router-dom'
import {Row, Col, Image, ListGroup, Button, Card, Form} from 'react-bootstrap'
import Ratings from '../components/Ratings'
import Loader from '../components/Loader'
import Message from '../components/Message'
import {listProductDetails, createProductReview} from '../actions/productActions'
import { PRODUCT_CREATE_REVIEW_RESET } from '../constants/productConstants'


function ProductView() { 
   const [qty, setQty] = useState(1)
   const [rating, setRating] = useState(0)
   const [comment, setComment] = useState('')

   const dispatch = useDispatch()
   const { id } = useParams()
   const navigate = useNavigate()

   const productDetails = useSelector(state => state.productDetails)
   const { loading, error, product } = productDetails

   const userLogin = useSelector(state => state.userLogin)
   const { userInfo } = userLogin

   const productCreateReview = useSelector(state => state.productCreateReview)
   const {
      loading: loadingProductReview,
      error: errorProductReview,
      success: successProductReview,
   } = productCreateReview

   useEffect(() => {
   if (successProductReview) {
      setRating(0)
      setComment('')
      dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
   }
      dispatch(listProductDetails(id))
   }, [dispatch, id, successProductReview])

   const addToCartHandler = () =>{
      navigate(`/cart/${id}?qty=${qty}`)
   }

   const submitHandler = (e) => {
      e.preventDefault()
      dispatch(createProductReview(
         id, {
            rating,
            comment
         }
      ))
  }

   return (
      <div>
         <Link to='/shop' className='btn-page-nav my-3'>Continue Shopping</Link>
         {loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message> : (
            <>
            <Row>
               <Col md={6}>
                  <Image src={product.image} alt={product.name} fluid />
               </Col>

               <Col md={3}>
                  <ListGroup variant="flush">
                     <ListGroup.Item>
                        <h3>{product.name}</h3>
                     </ListGroup.Item>

                     <ListGroup.Item>
                        <Ratings value={product.rating} text={`${product.num_reviews} reviews`} color={'#f8e825'} />
                     </ListGroup.Item>

                     {product.price === product.sale_price ? 
                        <ListGroup.Item>Price: ${product.price}</ListGroup.Item> :
                        <ListGroup.Item>Price: <span className='sale-price-text'>${product.sale_price}</span></ListGroup.Item> 
                     }

                     <ListGroup.Item>
                        Description: {product.description}
                     </ListGroup.Item>
                  </ListGroup>
               </Col>

               <Col md={3}>
                  <Card>
                     <ListGroup variant="flush">
                        <ListGroup.Item>
                           <Row>
                              <Col>Price:</Col>
                              {product.price === product.sale_price ? 
                                 <Col><strong>${product.price}</strong></Col> : 
                                 <Col>
                                    <strong className='reg-price-text'>${product.price}</strong>
		                              <strong className='sale-price-text'> ${product.sale_price}<p className='h6'>ON SALE!</p></strong>
                                 </Col>
                              }
                           </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                           <Row>
                              <Col>Status:</Col>
                              <Col>
                                 {product.count_in_stock > 0 ? 'In Stock' : 'Out of Stock'}
                              </Col>
                           </Row>
                        </ListGroup.Item>

                        {product.count_in_stock > 0 && (
                           <ListGroup.Item>
                              <Row>
                                 <Col>Quantity:</Col>
                                 <Col xs='auto' className='my-1'>
                                    <Form.Select value={qty} onChange={(e) => setQty(e.target.value)}>
                                       {[...Array(product.count_in_stock).keys()].map((x) => (
                                          <option key={x + 1} value={x + 1}>
                                             {x + 1}
                                          </option>
                                       ))}
                                    </Form.Select>
                                 </Col>
                              </Row>
                           </ListGroup.Item>
                        )}

                        <ListGroup.Item>
                        <div className = 'text-center'>
                           <Button onClick={addToCartHandler} disabled={product.count_in_stock === 0} type='button' variant='custom'>
                           <i className="fa-solid fa-cart-plus"></i> Add To Cart
                           </Button>
                        </div>
                        </ListGroup.Item>
                     </ListGroup>
                  </Card>
               </Col>
            </Row>
            <Row>
               <Col md={6}>
                  <h4>Reviews</h4>
                  {product.reviews.length === 0 && <Message variant='info'>No reviews yet</Message>}
                  <ListGroup variant='flush'>
                     {product.reviews.map((review) => (
                        <ListGroup.Item key={review._id}>
                           <strong>{review.name}</strong>
                           <Ratings value={review.rating} color='#f8e825' />
                           <p>{review.created_at.substring(0, 10)}</p>
                           <p>{review.comment}</p>
                        </ListGroup.Item>
                     ))}
                     <ListGroup.Item>
                        <h4>Write a review</h4>
                        {loadingProductReview && <Loader />}
                        {successProductReview && <Message variant='success'>Review Submitted</Message>}
                        {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}
                        {userInfo ? (
                           <Form onSubmit={submitHandler}>
                              <Form.Group controlId='rating'>
                                 <Form.Label>Rating</Form.Label>
                                 <Form.Control as='select' value={rating} onChange={(e) => setRating(e.target.value)} className='mb-3'>
                                    <option value=''>Select...</option>
                                    <option value='1'>1 - Poor</option>
                                    <option value='2'>2 - Fair</option>
                                    <option value='3'>3 - Good</option>
                                    <option value='4'>4 - Very Good</option>
                                    <option value='5'>5 - Excellent</option>
                                 </Form.Control>
                              </Form.Group>

                              <Form.Group controlId='comment'>
                                 <Form.Label>Review</Form.Label>
                                 <Form.Control
                                    as='textarea'
                                    row='5'
                                    value={comment}
                                    onChange={(e) => setComment(e.target.value)}
                                    className='mb-3'
                                 ></Form.Control>
                              </Form.Group>

                              <Button disabled={loadingProductReview} type='submit' variant='custom'>
                                 Submit
                              </Button>

                           </Form>
                        ) : (
                        <Message variant='info'>
                           If purchased here, please <Link to='/login' className='link-btn'>login</Link> to write a review.
                        </Message>
                        )}
                     </ListGroup.Item>
                  </ListGroup>
               </Col>
            </Row>
            </>
         )}
      </div>
   )
}

export default ProductView